<template>
	<div class="search">
		<div class="head">
			<div class="fan">
				<router-link to="/">
					<van-icon name="arrow-left" />
				</router-link>
			</div>	
			<div class="inp">
				<input type="text" placeholder="汉堡王" v-model="msg" @focus="jian()" @blur="pan()" @keyup.enter="changedata()">
			</div>
		</div>
		<div class="lishi">
			<p>
				<span>历史搜索</span>
				<span class="qin" @click="qin()"><van-icon name="delete" /></span>
			</p>
			<ul class="jilu">
				<li v-for="(obj,index) in item">
					<router-link to="/list">{{obj.lis}}</router-link>
				</li>
			</ul>
		</div>
		<!-- 热门搜索 -->
		<div class="lishi">
			<p>
				<span>热门搜索</span>
			</p>
			<ul class="jilu">
				<li v-for="(obj,index) in remen">
					<router-link to="">{{obj.name}}</router-link>
				</li>
			</ul>
		</div>
		<div class="jian">
			<van-field
			  readonly
			  clickable
			  :value="value"
			  @touchstart.native.stop="show = true"
			/>
			<van-number-keyboard
			  v-model="value"
			  :show="show"
			  :maxlength="20"
			  @blur="show = false"
			/>
		</div>
		
	</div>
</template>

<script>
	import Vue from 'vue';
	import { Icon } from 'vant';
	import cache from '../cache.js'
	console.log(cache);
	Vue.use(Icon);
	
	import { NumberKeyboard,Field } from 'vant';
	Vue.use(NumberKeyboard);
	Vue.use(Field);
	export default {
		name:'search',
		data(){
			return{
				msg:'',
				show: false,
				value: '',
				// item:catch.get('arr'),
				item:cache.get('Search'),
				remen:[
					{
						name:'水煮肉片'
					},
					{
						name:'粉蒸排骨'
					},
					{
						name:'大盘鸡'
					},
				]
			}
		},
		methods:{
			jian(){
				this.show = true
			},
			pan(){
				this.show = false
			},
			// 回车发送
			changedata(){
				if(this.msg){
					console.log(this.msg.trim());
					this.item.push({
						lis:this.msg.trim()
					})
					this.msg = ''
					// 写入缓存
					localStorage.setItem('Search',JSON.stringify(this.item))
					this.$router.push('List')
				}
			},
			qin(){
				this.item.splice(0)
				// cache.get('Search',this.item)
				localStorage.setItem('Search',JSON.stringify(this.item))
			}
		},
	}
</script>

<style scoped="scoped" lang="less">
	.search{
		height: auto;
		width: 100%;
	}
	.head{
		width: 100%;
		height: 40px;
		border-bottom: 1px solid #CCCCCC;
		margin-top: 10px;
		.fan{
			width: 15%;
			height: 30px;
			text-align: center;
			line-height: 30px;
			float: left;
			margin-top: 5px;
		}
		.inp{
			width: 85%;
			height: 30px;
			float: right;
			input{
				width: 90%;
				height: 100%;
				margin: 0 auto;
				border: none;
				outline: none;
				font-size: 13px;
				border-radius: 15px;
				background: #CCCCCC;
				text-indent: 10px;
				padding: 0;
			}
		}
	}
	.lishi{
		width: 100%;
		height: 100px;
		p{
			margin: 0;
			font-size: 16px;
			padding: 5px 20px;
			.qin{
				float: right;
				font-size: 18px;
			}
		}
	}
	.lishi ul{
		width: 100%;
		height: auto;
		li{
			width: auto;
			height: 20px;
			float: left;
			font-size: 13px;
			margin: 3px 12px;
			padding: 2px 4px;
			border-radius: 10px;
			border: 1px solid #008FFF;
			a{
				color: #CCCCCC;
			}
		}
	}
	.jian{
		.van-cell{
			display: none;
		}
	}
</style>
