<template>
	<div class="mima">
		<!-- 顶部 -->
		<div class="fanhui">
			<router-link to="/wode"><van-icon name="cross" /></router-link>
			<router-link to="/sign" class="rt">手机号登录</router-link>
		</div>
		<!-- 中间部分 -->
		<div class="min">
			<h2>密码登录</h2>
			<form action="">
				<van-form @submit="onSubmit">
				  <van-field
				    v-model="username"
				    name="手机/用户名"
				    placeholder="手机/用户名"
				    :rules="[{ required: true, message: '请填写用户名'}]"
					
				  />
				  <van-field
				    v-model="password"
				    type="password"
				    name="密码"
				    placeholder="密码"
				    :rules="[{ required: true, message: '请填写密码'}]"
				  />
				  <div style="margin: 16px;">
				    <van-button round block type="info" native-type="submit">
				      登录
				    </van-button>
				  </div>
				</van-form>
				<div class="zhaohui"><router-link to="">找回密码</router-link></div>
			</form>
		</div>
	</div>
	
</template>

<script>
	import cache from '../cache.js';
	import Vue from 'vue';
	import { Form,Field } from 'vant';
	Vue.use(Field)
	Vue.use(Form);
	export default {
	  data() {
	    return {
	      username: '',
	      password: '',
		  newarr:[]
	    };
	  },
	  methods: {
	    onSubmit(values) {
		  var userMsg=JSON.parse(localStorage.getItem('vals'))
		  if(values["手机/用户名"]==userMsg[0].user && values["密码"]==userMsg[0].psdd){
			  this.$router.push({name:'Home'})
			   alert('登录成功')
			   this.$store.state.yhm = this.username
		  }else{
			  alert('密码或者账号错误')
		  }
		  var arrs = this.newarr.push({user:this.username,psdd:this.password})
		  localStorage.setItem('mima',JSON.stringify(this.newarr))
	    },
	  },
	};
</script>

<style scoped lang="less">
	.mima{
		width: 96%;
		height: 600px;
		margin: 20px 2%;
		overflow: hidden;
		.fanhui{
			line-height: 30px;
			height: 30px;
			width: 100%;
			font-size: 36px;
			a{
				color: black;
			}
			.rt{
				float: right;
				font-size: 20px;
			}
		}
	}
	.mima>.min{
		width: 100%;
		height: 400px;
		overflow: hidden;
		h2{
			text-align: center;
		}
	}
	.zhaohui{
		width: 100%;
		height: 25px;
		text-align: right;
		a{
			font-size: 18px;
			margin-right: 20px;
		}
	}
</style>
