		},
		methods: {
			onClickLeft() {
				this.$router.go(-1)
			},
			onChange(index) {
				this.showList = false;
				this.chosenCoupon = index;
			},
			onExchange(code) {
				this.coupons.push(coupon);
			},
			tan(){
				this.show = !this.show
			},
			yous(){
				this.showList = !this.showList
			},
			onClickLefts() {
			     this.show = !this.show
				 console.log(111);
			},
			Complete(){
				this.$router.push('Complete')
			}
		},
		mounted(){
			console.log(this.$store.state.nuwGood);
			// console.log(this.$store.state.nuwGood[0].money);
			// console.log(this.$store.state.nuwGood.num);
			 this.dai = 0
			for(var i=0;i<this.$store.state.nuwGood.length;i++){
				this.dai += this.$store.state.nuwGood[i].money * this.$store.state.nuwGood[i].num + 5
			}
			
		},
		filters: {
			mone(value) {
				return '待支付￥' + value
			},
			hui(value) {
				return '已优惠￥' + value
			},
			chen(value) {
				return '×' + value
			},
			mon(value) {
				return '￥' + value
			}
		}
	}
